.cursor {
    font-size: 25px;
    -webkit-animation: blink .85s step-end infinite;
            animation: blink .85s step-end infinite;
}

@-webkit-keyframes blink {
  from, to { color: transparent }
  50% { color: black; }
}

@keyframes blink {
  from, to { color: transparent }
  50% { color: black; }
}

.animate {
	-webkit-animation: changeText 600ms linear;
	        animation: changeText 600ms linear;
}


@-webkit-keyframes changeText {
  from, to { background-color: white; color: black;}
  50% { background-color: #2EB6E2FC; color: #2EB6E2FC;}
  100% { background-color: white; color: black;}
}


@keyframes changeText {
  from, to { background-color: white; color: black;}
  50% { background-color: #2EB6E2FC; color: #2EB6E2FC;}
  100% { background-color: white; color: black;}
}

